export default function OrderToPhone(data) {
    var text = `ORDEN *#${data.numero}*\n` +
        "CLIENTE: " +
        `*${(data.nombre || "").trim()}*\n` +
        "TELEFONO: " +
        `*${(data.telefono || "").trim()}*\n` +
        "\n" +
        "PRODUCTOS:\n" +
        (data.detalle.map(x => {
            return "```" + padR(x.cantidad) + " " + (x.descripcion || "").trim() + "```";
        }).join("\n")) +
        "\n" +
        "\n" +
        (data.nota && ("NOTAS: " + "*" + data.nota.trim() + "*\n")) +
        "METODO DE PAGO: " +
        "*" + data.FormaPago + " $" + data.total.toFixed(2) + "*\n" +
        "DIRECCION: " +
        `*${(data.direccion || "").trim()}* 🏠*${(data.referencia || "").trim()}*\n` +
        "📍 https://www.google.com/maps/dir/-" + data.lat + "," + data.lng;

    //return `https://wa.me/${data.phone}/?text=` + encodeURIComponent(text);
    return "whatsapp://send?phone=" + data.phone + "&text=" + encodeURIComponent(text);
}

function padR(number, length) {
    var str = number.toString();
    if (str.length === length) return str;
    if (str.length >= length) return str.substr(0, length);

    var dif = length - str.length;
    for (let i = 0; i < dif; i++) {
        str += " ";
    }
    return str;
}